import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import Condividi from "../../components/Condividi.vue";
import DataHeader from "../../components/DataHeader.vue";
import { ConsiglioInforma } from "../../js/consiglioinforma";
import videojs from 'video.js';
import 'video.js/dist/video-js.css';

export default {
    name: "app",
    components: {
        Footer,
        Header,
        Condividi,
        DataHeader
    },
    data: function () {
        $(document).ready(renderNotizia);
        return {
            sezione: ConsiglioInforma.Video['SEDUTE']
        };
    }
};

function renderNotizia() {
    var urlParams = new URLSearchParams(window.location.search);
    renderContenuto();

    function renderContenuto() {
        var urlSezione = apibase.baseurl + '10535';
        return $.ajax({
            url: urlSezione,
            success: function (result) {
                if (result && result.content) {
                    $("#title").html(result.content[0].title);
                    // var data = StringRenderer.renderDataTestuale(result.properties.date);
                    // $("#subtitle").text(data);
                    $("#container_base").append(renderVideo(result.content[0]));
                    $('#statoDellaDiretta').html(result.content[0].properties.stato_della_diretta);
                    videojs('#videoDiretta');
                } else {
                    window.location = process.env.BASE_URL + "notFound.html";
                }
            }, error: function (res_err) {
                window.location = process.env.BASE_URL + "notFound.html";
            }
        });
    }

    function renderVideo(result) {
        if (result) {
            let sorgenti = [];
            if (result.properties.server_apple) {
                sorgenti.push('<source src="' + result.properties.server_apple + '">');
            }
            if (result.properties.server) {
                sorgenti.push('<source src="' + result.properties.server + '"">');
            }
            if (result.properties.server_android) {
                sorgenti.push('<source src="' + result.properties.server_android + '">');
            }
            return '<video id="videoDiretta" class="video-js vjs-default-skin vjs-big-play-centered img-fluid vjs-16-9" controls>' + sorgenti.join('') + '</video>';
        }
        return '';
    }
}


